import permission from "../controllers/permission";
export default (app) => {

    /* Routes for permission */

    app.route("/permission/addPermission").post(permission.addPermission);

    app.route("/permission/getAllPermission").get(permission.getAllPermission);

    app.route("/permission/updatePermission").put(permission.updatePermission);

    app.route("/permission/deletePermission/:idPermission").delete(permission.deletePermission);

    return app;
};